import _map from 'lodash/map'
import _filter from 'lodash/filter'
import _forEach from 'lodash/forEach'
import _isEmpty from 'lodash/isEmpty'
import _join from 'lodash/join'
import parse from 'csv-parse'

/**
 * Removes special characters at the end of the string.
 * @param {string} text
 */
export function trimSpecialChars(text) {
  return text.replace(/[,:;]*$/, '')
}

/**
 * Gets the link to google map for the location.
 * @param {string} location
 */
export function getLocationLink(location) {
  return `https://maps.google.com/?q=${encodeURI(location)}`
}

/**
 * Returns the label for service type code.
 * @param {string} serviceType
 */
export function mapServiceType(serviceType) {
  return {
    HER: '中醫',
    PHY: '普通科門診',
    ACU: '針灸',
    BST: '跌打',
    SPL: '腸胃肝臟科專科',
    PHS: '物理治療',
    ROE: '牙科',
    CHI: '脊醫',
    SPO: '運動治療',
  }[serviceType]
}

/**
 * Removes empty items from the array.
 * @param {array} array
 */
export function removeBlankItems(array) {
  return _filter(array, item => !_isEmpty(item))
}

/**
 * Gets all regions and locations from the doctor list and returns a map to the list of locations.
 * @param {array} doctors
 * @returns map of region -> array of locations
 */
export function getRegions(doctors) {
  const groups = {}
  const locations = {}
  _forEach(doctors, doctor => {
    if (!locations[doctor.location]) {
      locations[doctor.location] = true

      if (!groups[doctor.region]) {
        groups[doctor.region] = []
      }
      groups[doctor.region].push(doctor.location)
    }
  })
  return groups
}

/**
 * Parses and converts the data csv to an array of doctor objects.
 * @param {*} data
 */
export function parseCsvData(data) {
  return new Promise(resolve => {
    parse(data, {
      from_line: 2
    }, function (err, output) {
      const result = _map(output, row => {
        return {
          id: row[3],
          name: row[4],
          serviceTypes: row[0].split('/'),
          region: row[12],
          location: row[11],
          address: [
            row[9],
            row[10]
          ],
          tel: row[13],
          openDays: removeBlankItems([
            _join(removeBlankItems([row[23], row[24]]), ': '),
            _join(removeBlankItems([row[25], row[26]]), ': '),
            _join(removeBlankItems([row[27], row[28]]), ': '),
            _join(removeBlankItems([row[29], row[30]]), ': '),
          ]),
          prices: removeBlankItems(row[31].split(',')),
          medicine: removeBlankItems(row[33].split(','))
        }
      })
      resolve(result)
    })
  })
}
