import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import _map from 'lodash/map'
import _includes from 'lodash/includes'

const PickerContainer = styled.div`
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  overflow: auto;
`

const PickerOption = styled.div`
  cursor: pointer;
  margin: 2.5px;
  padding: 5px 7.5px;
  border-radius: 5px;
  transition background 0.3s;
  &:hover {
    background: ${props => props.theme.pickerHover};
  }
  &.active {
    background: ${props => props.theme.pickerActive};
  }
`

function Picker({ className, items, values, onSelect }) {
  return (
    <PickerContainer className={className}>
      {_map(items, item => (
        <PickerOption
          key={item}
          className={_includes(values, item) ? 'active' : ''}
          onClick={() => onSelect && onSelect(item)}
        >
          { item }
        </PickerOption>
      ))}
    </PickerContainer>
  )
}

Picker.propTyeps = {
  className: PropTypes.string,
  items: PropTypes.arrayOf(PropTypes.string),
  values: PropTypes.arrayOf(PropTypes.string),
  onSelect: PropTypes.func
}

export default Picker
