import React, { useMemo } from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import _map from 'lodash/map'
import _join from 'lodash/join'

import { mapServiceType, trimSpecialChars, getLocationLink } from '../utils/data'

const Card = styled.div`
  box-sizing: border-box;
  background: ${props => props.theme.cardBg};
  border: 1px solid ${props => props.theme.cardBorder};
  color: ${props => props.theme.cardText};
  padding: 10px;
  border-radius: 4px;
  box-shadow: 2px 4px 5px 1px ${props => props.theme.cardShadow};
`

const CardHeader = styled.div`
  display: flex;
  align-items: center;
`

const DoctorName = styled.div`
  flex: 1;
  font-weight: bold;
  font-size: 18px;
`

const CardButton = styled.span`
  margin-left: 10px;
  cursor: pointer;
  font-size: 22px;
`

const ServiceList = styled.div`
  margin: 0 -2.5px;
  display: flex;
  flex-wrap: wrap;
`

const ContactInfos = styled.div`
  margin: 10px 0;
`

const ServiceTag = styled.div`
  margin: 10px 2.5px 0 2.5px;
  border: 1px solid ${props => props.theme.cardText};
  padding: 5px 15px;
  border-radius: 20px;
  :not(:first-child) {
    margin-left: 5px;
  }
`

const DoctorAddress = styled.a`
  color: ${props => props.theme.cardText};
  font-weight: bold;
  text-decoration: none;
  display: flex;
  align-items: center;
`

const DoctorInfo = styled.div`
  line-height: 22px;
  display: flex;
  align-items: center;
`

const Icon = styled.span`
  font-size: 18px;
`

function DoctorCard({ doctor }) {
  const address = useMemo(() => _join(doctor.address, ', '), [doctor.address])

  const onShare = () => {
    navigator.share({
      title: doctor.name,
      text: `${doctor.name}\n地址:${address}\n電話:${doctor.tel}`
    })
  }

  const onCopy = () => {
    navigator.clipboard.writeText(`${doctor.name}\n地址:${address}\n電話:${doctor.tel}`)
  }

  return (
    <Card>
      <CardHeader>
        <DoctorName>{ doctor.name }</DoctorName>
        {navigator.share ? (
          <CardButton className="material-icons" onClick={onShare}>share</CardButton>
        ) : null}
        {navigator.clipboard ? (
          <CardButton className="material-icons" onClick={onCopy}>file_copy</CardButton>
        ) : null}
      </CardHeader>
      <ServiceList>
        {_map(doctor.serviceTypes, serviceType => (
          <ServiceTag key={serviceType}>{ mapServiceType(serviceType) }</ServiceTag>
        ))}
      </ServiceList>
      <ContactInfos>
        <DoctorAddress href={getLocationLink(_join(doctor.address, ','))} target="_blank">
          <Icon className="material-icons">location_on</Icon>
          <span>{ address }</span>
        </DoctorAddress>
        <DoctorInfo>
          <Icon className="material-icons">local_phone</Icon>{ doctor.tel }
        </DoctorInfo>
      </ContactInfos>
      <DoctorInfo><b>會員專享:</b></DoctorInfo>
      {_map(doctor.prices, (price, index) => {
        let labelPrefix = ''
        if (doctor.prices.length === doctor.serviceTypes.length && doctor.prices.length > 1) {
          labelPrefix = mapServiceType(doctor.serviceTypes[index])
        }
        let medicine = '不包括任何藥物'
        if (doctor.medicine && doctor.medicine.length > index && doctor.medicine[index] !== 'NA') {
          medicine = `包括${doctor.medicine[index]}日藥物`
        }
        return (
          <DoctorInfo key={index}>
            { labelPrefix }診金: ${ price } ({medicine})
          </DoctorInfo>
        )
      })}
      {_map(doctor.openDays, (openDay, index) => (
        <DoctorInfo key={index}>{ trimSpecialChars(openDay) }</DoctorInfo>
      ))}
    </Card>
  )
}

DoctorCard.propTypes = {
  doctor: PropTypes.any
}

export default DoctorCard
