import React, { createContext, useContext, useEffect, useState, useMemo } from 'react'
import _filter from 'lodash/filter'
import _isEmpty from 'lodash/isEmpty'
import _includes from 'lodash/includes'
import _slice from 'lodash/slice'

import { fetchDoctorList } from './api/doctors'
import { getRegions } from './utils/data'

const DoctorListContext = createContext()

export function StoreProvider({ children }) {
  const [loading, setLoading] = useState(true)
  const [regionOptions, setRegionOptions] = useState({})
  const [locationFilters, setLocationFilters] = useState([])
  const [fullList, setFullList] = useState([])

  useEffect(() => {
    // fetch remote doctor list on mounted
    fetchDoctorList().then(doctors => {
      setFullList(doctors)
      setRegionOptions(getRegions(doctors))
      setLoading(false)
    })
  }, [])

  const contextValue = {
    fullList,
    regionOptions,
    locationFilters,
    setLocationFilters,
    loading
  }

  return (
    <DoctorListContext.Provider value={contextValue}>
      { children }
    </DoctorListContext.Provider>
  )
}

/**
 * Gets the filtered doctor list of the specified page.
 * @param {*} page the current page number, starting at 0
 * @param {*} pageSize the number of items in a page
 * @returns [items of the page, total number of page]
 */
export function useDoctorList(page = 0, pageSize = 10) {
  const { loading, locationFilters, fullList } = useContext(DoctorListContext)

  const items = useMemo(() => {
    if (_isEmpty(locationFilters)) {
      return fullList // return full list if no filter is applied
    }
    return _filter(fullList, doctor => {
      return _includes(locationFilters, doctor.location) // show a doctor if the location matches any of the filter
    })
  }, [locationFilters, fullList])

  const pageItems = _slice(items, page * pageSize, (page + 1) * pageSize)
  const totalPages = Math.ceil(items.length / pageSize)

  return [loading, pageItems, totalPages]
}

/**
 * Gets the applied filters and the function to manipulate the filter list.
 * @returns [selectedFilters, function to toggle a filter]
 */
export function useLocationFilters() {
  const { locationFilters, setLocationFilters } = useContext(DoctorListContext)
  const toggleFilter = (filter) => {
    let newFilters
    if (_includes(locationFilters, filter)) {
      newFilters = _filter(locationFilters, f => f !== filter)
    } else {
      newFilters = locationFilters.concat(filter)
    }
    setLocationFilters(newFilters)
  }
  return [locationFilters, toggleFilter]
}

/**
 * @returns available options of locations
 */
export function useRegions() {
  const { regionOptions } = useContext(DoctorListContext)
  return regionOptions
}
