import React, { useState } from 'react'
import styled from 'styled-components'
import _map from 'lodash/map'

import { useLocationFilters, useRegions } from '../store'
import Picker from './Picker'

const PanelContainer = styled.div`
  position: sticky;
  top: 0;
  box-shadow: 2px 2px 6px 2px ${props => props.theme.shadow};
  border-bottom: 1px solid ${props => props.theme.panelBorder};
  color: ${props => props.theme.panelText};
  background: ${props => props.theme.panelBg};
  z-index: 1;
`

const Underlay = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1;

  ${props => props.active ? '' : 'display: none;'}
`

const SearchWrapper = styled.div`
  position: relative;
  z-index: 2;
`

const SearchContainer = styled.div`
  transition: height 0.3s;
  overflow: hidden;
  height: ${props => props.expand ? '135px' : '0'};
`

const PickersContainer = styled.div`
  height: 125px;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
`

const RegionPicker = styled(Picker)`
  margin-right: 5px;
  @media screen and (min-width: 1024px) {
    margin-right: 15px
  }
`

const LocationPicker = styled(Picker)`
  border-left: 1px solid ${props => props.theme.panelSeparator};
  padding-left: 5px;
  width: 75%;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
  @media screen and (min-width: 1024px) {
    width: 50%;
    padding-left: 15px;
  }
`

const FilterList = styled.div`
  margin: 0 15px;
  min-height: 42px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
`

const FilterHint = styled.span`
  margin-left: 5px;
  color: ${props => props.theme.panelHint};
`

const Filter = styled.div`
  padding: 5px;
`

const FilterTag = styled.div`
  cursor: pointer;
  background: ${props => props.theme.tagBg};
  color: ${props => props.theme.tagText};
  border-radius: 20px;
  padding: 5px 10px 5px 15px;
  display: flex;
  align-items: center;
  > span.material-icons {
    font-size: 18px;
  }
`

function SearchPanel () {
  const [locationFilters, toggleFilter] = useLocationFilters()
  const regions = useRegions()
  const [expand, setExpand] = useState(false)

  const [region, setRegion] = useState(null) // current selection of region
  const locations = regions && regions[region]

  return (
    <PanelContainer>
      <Underlay active={expand} onMouseEnter={() => setExpand(false)} onTouchStart={() => setExpand(false)} />
      <SearchWrapper onMouseEnter={() => setExpand(true)} onTouchStart={() => setExpand(true)}>
        <FilterList>
          <span className="material-icons">search</span>
          {locationFilters.length === 0 ? (
            <FilterHint>按地區搜索</FilterHint>
          ) : (
            _map(locationFilters, locationFilter => (
              <Filter key={locationFilter}>
                <FilterTag onClick={() => toggleFilter(locationFilter)}>
                  <span>{ locationFilter }</span>
                  <span className="material-icons">close</span>
                </FilterTag>
              </Filter>
            ))
          )}
        </FilterList>
        <SearchContainer expand={expand}>
          <PickersContainer>
            <RegionPicker
              items={Object.keys(regions)}
              values={[region]}
              onSelect={val => setRegion(val)}
            />
            <LocationPicker
              items={locations}
              values={locationFilters}
              onSelect={val => toggleFilter(val)}
            />
          </PickersContainer>
        </SearchContainer>
      </SearchWrapper>
    </PanelContainer>
  )
}

export default SearchPanel
