import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import _map from 'lodash/map'

import LoadingSpinner from './LoadingSpinner'
import SearchPanel from './SearchPanel'
import DoctorCard from './DoctorCard'
import { useDoctorList, useLocationFilters } from '../store'

const ListContainer = styled.div`
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: ${props => props.theme.appBg};
`

const CardListWrapper = styled.div`
  flex: 1;
`

const CardList = styled.div`
  margin: 0 auto;
  max-width: 1280px;
  display: flex;
  flex-wrap: wrap;
`

const Pager = styled.div`
  position: sticky;
  bottom: 0;
  box-shadow: 2px 2px 6px 2px ${props => props.theme.shadow};
  color: ${props => props.theme.panelText};
  background: ${props => props.theme.panelBg};
  border-top: 1px solid ${props => props.theme.panelBorder};
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
`

const PagerButton = styled.span`
  margin: 0 20px;
  cursor: pointer;
  ${props => props.disabled ? `color: ${props.theme.panelDisabledText};` : ''}
`

const CardWrapper = styled.div`
  box-sizing: border-box;
  width: 100%;
  padding: 15px;
  > * {
    height: 100%;
  }
  @media screen and (min-width: 1024px) {
    width: 50%;
    padding: 20px;
  }
  @media screen and (min-width: 1280px) {
    width: 33.3333%;
  }
`

export default function DoctorList() {
  const [page, setPage] = useState(0)
  const [loading, doctors, totalPages] = useDoctorList(page, 12) // use 12 items per page for all layouts
  const [filters] = useLocationFilters()

  // reset to page 1 if filter is updated
  useEffect(() => {
    setPage(0)
  }, [filters])

  if (loading) {
    return (
      <ListContainer>
        <LoadingSpinner />
      </ListContainer>
    )
  }

  return (
    <ListContainer>
      <SearchPanel />
      <CardListWrapper>
        <CardList>
          {_map(doctors, (doctor, index) => (
            <CardWrapper key={index}>
              <DoctorCard doctor={doctor} />
            </CardWrapper>
          ))}
        </CardList>
      </CardListWrapper>
      <Pager>
        <PagerButton
          className="material-icons"
          disabled={page === 0}
          onClick={() => setPage(Math.max(0, page - 1))}
        >
          navigate_before
        </PagerButton>
        <span>第 { page + 1 } / { totalPages } 頁</span>
        <PagerButton
          className="material-icons"
          disabled={page >= totalPages - 1}
          onClick={() => setPage(Math.min(totalPages - 1, page + 1))}
        >
          navigate_next
        </PagerButton>
      </Pager>
    </ListContainer>
  )
}
